@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;300;400;500;600;700&display=swap");
@import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.14.0/css/all.min.css");
@import "./default";

body {
  font-family: "Poppins", sans-serif !important;
  color: $headingColor;
  background: $lightGray;

  a {
    color: $themeColor;
  }
  .nav-link {
    font-weight: 500;
    padding: 20px 32px !important;
    margin: 0 5px;
    @media(max-width:991px){
      margin:0 auto;
      display: inline;
    }
    &:hover,
    &.active {
      color: $whiteColor !important;
      background: $themeColor;
      border-top-left-radius: 30px;
      border-bottom-right-radius: 30px;
      box-shadow: -4px 0px 1px 1px #a9a9ab;
    }
  }
  h2 {
    font-size: 28px;
  }
  .btn-primary:not(:disabled):not(.disabled).active:focus,
  .btn-primary:not(:disabled):not(.disabled):active:focus,
  .show > .btn-primary.dropdown-toggle:focus,
  .btn-primary.focus,
  .btn-primary:focus {
    box-shadow: none !important;

  }
  .btn {
    padding: 10px 50px;
    border-radius: 30px;
    border: 2px solid $themeColor;
    position: relative;
  }
  .btn-primary {
    @include PrimaryColorAndBorder();

    &:hover,
    &:not(:disabled):not(.disabled):active {
      background-color: $themeDarkColor;
      color: $whiteColor;
      border-color: $themeDarkColor;
    }
    &:disabled{
      background-color: $grayColor;
      color: $whiteColor;
      border-color: $grayColor;
      &:after{
        display: none;
      } 
    }
    &:after {
      content: "";
      position: absolute;
      bottom: -14px;
      left: 50%;
      width: 90%;
      height: 10px;
      background: $themeColor;
      filter: blur(12px);
      transform: translate(-50%, -50%);
      opacity: 0.8;
    }
  }
  .navbar {
    background: #eaeaf4;
    position: fixed;
    z-index: 2;
    box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.6);
    display: flex;
    justify-content: space-between;
    padding: 0 1rem;
    .navbar-brand {
      margin-left: 20px;
    }
    .header-search-form {
      position: relative;
      @media(max-width:991px){
        min-width: 580px;
        margin: 0 auto;
      }
      @media(max-width:767px){
        min-width: 100%;
      }
      input {
        min-width: 580px;
        border-radius: 30px;
        padding: 10px 20px;
        height: 40px;
        @media(max-width:991px){
          min-width: 100%;
          margin: 0 auto 30px;
        }
        @media(max-width:767px){
          min-width: 100%;
          margin-bottom:30px;
        }
      }
      button {
        position: absolute;
        right: 23px;
        border: none;
        padding: 0;
        top: 8px;
        transform: rotate(-13deg);
        &:hover {
          background: transparent;
          border-color: transparent;
        }
        &.btn-outline-success:not(:disabled):not(.disabled):active,
        &:focus {
          background: transparent;
          border-color: transparent;
          box-shadow: none;
        }
        i {
          font-size: 24px;
          color: $themeColor;
        }
      }
    }
  }
  .blog-page {
    padding: 100px 0 0px;
    .blog-text {
      a {
        text-decoration: none;
        color: $headingColor;
      }
    }
    .sb-avatar {
      span {
        color: $whiteColor;
      }
    }
    .img-wrap {
      background: #ccc;
      height: 300px;
      position: relative;
      &:before {
        background: rgba(0, 0, 0, 0.36);
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
      }
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        margin: 0 auto;
      }
      .overlay-text {
        position: absolute;
        bottom: 20px;
        width: 100%;
        padding: 0 20px;
        h4 {
          color: #fff;
          margin: 0;
          font-size: 18px;
        }
        a {
          text-decoration: none;
        }
        .category-tag {
          background: $themeColor;
          padding: 3px 10px;
          display: inline-block;
          margin-bottom: 5px;
          color: $whiteColor;
          font-size: 12px;
          font-weight: 400;
        }
      }
    }
    h6,
    span {
      color: #b5b5b5;
    }

    .read-more-text {
      color: $themeColor;
    }
    .social-listing {
      padding: 0;
      li {
        list-style: none;
        margin: 0 3px;
        a {
          color: $themeColor;
          display: flex;
          align-items: center;
          justify-content: center;
          height: 40px;
          width: 40px;
          border-radius: 100%;
          border: 2px solid $themeColor;
          text-decoration: none;
          &:hover {
            background: $themeColor;
            color: #fff;
          }
          i {
            font-size: 16px;
          }
        }
        &.fcebook-icon {
          a {
            &:hover {
              background: $facebookColor;
              border-color: $facebookColor;
            }
          }
        }
        &.twitter-icon {
          a {
            &:hover {
              background: $twitterColor;
              border-color: $twitterColor;
            }
          }
        }
        &.linkedin-icon {
          a {
            &:hover {
              background: $linkedinColor;
              border-color: $linkedinColor;
            }
          }
        }
        &.instagram-icon {
          a {
            &:hover {
              background: $instagramColor;
              border-color: $instagramColor;
            }
          }
        }
      }
    }
    .blog-card {
      padding: 20px 10px;
      background: #9b9ec7;
      box-shadow: 1px 1px 12px 5px rgba(204, 204, 204, 0.17);
      border-top-right-radius: 30px;
      border-bottom-right-radius: 30px;
      height: 100%;
      @media (max-width:991px){
        display: none;
      }
      .card-listing {
        margin: 10px 0;
        border-bottom: 1px solid #ccc;
        padding-bottom: 10px;
        .user-icon-circle {
          height: 40px;
          width: 40px;
          border-radius: 100%;
          background: #ccc;
          overflow: hidden;
          .img-fluid{
            height: 100%;
            width: 100%;
            object-fit: cover;
          }
        }
        .blog-card-text {
          a {
            text-decoration: none;
            color: $whiteColor;
          }
          h6 {
            width: 170px;
            text-overflow: ellipsis;
            white-space: nowrap;
            overflow: hidden;
            font-size: 14px;
          }
          p {
            font-size: 11px;
            line-height: 13px;
            color: $whiteColor;
            width: 170px;
            text-overflow: ellipsis;
            white-space: nowrap;
            overflow: hidden;
            font-weight: 300;
          }
        }
        .rection-wrap {
          a {
            font-size: 11px;
            color: $whiteColor;
            position: relative;
            align-items: center;
            &:after {
              position: absolute;
              top: 0;
              right: -9px;
              content: "|";
              color: #ccc;
            }
            &:last-child:after {
              display: none;
            }
            i {
              margin-left: 5px;
              margin-top: -1px;
            }
            &:hover {
              text-decoration: none;
            }
          }
        }
      }
      &.trending-category-listing {
        .user-icon-circle {
          border-radius: 0;
          img {
            object-fit: cover;
            max-width: 100%;
            height: 100%;
          }
        }
      }
      .main-heading {
        margin-bottom: 20px;
        // h4 {
        //   font-size: 20px;
        // }
      }
      .rection-wrap {
        a {
          span {
            color: $whiteColor;
          }
        }
      }
    }
    .post-comments {
      .card-listing {
        border-radius: 12px;
        box-shadow: 0px 1px 5px 0px #a9a9ab;
        .user-icon-circle {
          height: 50px;
          width: 50px;
          border-radius: 100%;
          background: #ccc;
        }
      }
    }
    .form-wrap {
      padding: 40px 30px 40px;
      background: #fff;
      box-shadow: 1px 1px 12px 5px rgba(204, 204, 204, 0.17);
      margin-top: 40px;
      border-radius: 6px;
      position: relative;
      height: 100%;
      @media(max-width:991px){
        height: auto;
      }
      input {
        font-size: 14px;
      }
      textarea {
        height: 120px;
      }
      .circle-shape-bg {
        position: absolute;
        right: -80px;
        bottom: -100px;
        z-index: -1;
        @media (max-width: 1199px) {
         display: none;
        }
      }
      .net-shape-bg {
        position: absolute;
        left: -120px;
        z-index: -1;
        top: -180px;
        @media (max-width: 1199px) {
          left: -49px;
        }
        @media (max-width: 991px) {
          left: -39px;
          top: -180px;
        }
      }
    }
  }
  #custom-scrollbar {
    scroll-behavior: smooth;
    overflow-y: auto;
    max-height: 100%;
    overflow-x: hidden;
    padding: 0 0;
    margin: 0;
    @media (max-width: 767px) {
      max-height: 150px;
    }
    &::-webkit-scrollbar {
      width: 7px;
      height: 7px;
    }
    &::-webkit-scrollbar-thumb {
      background-color: #7f7f7f;
      outline: 1px solid slategrey;
      border-radius: 7px;
      text-shadow: 0 0 black;
    }
    &::-webkit-scrollbar-track {
      -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.3);
      box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.3);
      border-radius: 7px;
    }
  }
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.headerImage {
  width: 6rem;
  height: 6rem;
}

.headerHomeImage {
  width: 8rem;
  height: 8rem;
}

.backToHome {
  margin: 3rem 0 0;
}
.auth-modal {
  .modal-header,
  .modal-footer {
    border: none;
    position: relative;
    padding: 0;
    .close {
      margin: 0;
      position: absolute;
      right: -6px;
      top: -10px;
      z-index: 2;
    }
    button:focus {
      border: none;
      outline: none;
    }
  }
  .modal-content {
    border-radius: 0;
    border: none;
    background-clip: initial;
  }
  .auth-form-wrap {
    form {
      padding: 10px 40px 30px;
      @media (max-width: 767px) {
        padding-left: 10px;
        padding-right: 10px;
      }
    }
    .btn-primary {
      border-radius: 5px;
      padding: 6px 70px;
      background: $themeColor;
      color: $whiteColor;
      position: relative;
      z-index: 2;
      &:hover {
        background: $themeDarkColor;
        color: $whiteColor;
      }
    }
    .auth-links {
      font-size: 14px;
      a {
        color: $headingColor;
        text-decoration: none;
        font-size: 14px;
        &:hover {
          color: $themeColor;
        }
      }
    }
    .auth-socail-btn-wrap {
      .btn {
        padding: 6px 33px;
        &.facebook-btn {
          background: $facebookColor;
          color: $whiteColor;
          margin-right: 15px;
          border-color: $facebookColor;
          &:hover {
            background: transparent;
            color: $facebookColor;
          }
          @media (max-width: 767px) {
            margin-right: 0;
          }
        }
        &.google-btn {
          background: $googleColor;
          color: $whiteColor;
          margin-right: 15px;
          border-color: $googleColor;
          &:hover {
            background: transparent;
            color: $googleColor;
          }
          @media (max-width: 767px) {
            margin-right: 0;
          }
        }
        &.linkedin-btn {
          background: $linkedinColor;
          color: $whiteColor;
          margin-right: 15px;
          border-color: $linkedinColor;
          &:hover {
            background: transparent;
            color: $linkedinColor;
          }
          @media (max-width: 767px) {
            margin-right: 0;
          }
        }
        &.twitter-btn {
          background: $twitterColor;
          color: $whiteColor;
          border-color: $twitterColor;
          &:hover {
            background: transparent;
            color: $twitterColor;
          }
        }
        @media (max-width: 767px) {
          margin-bottom: 10px;
          width: 100%;
        }
      }
    }
    p {
      color: $darkgrayColor;
      font-size: 14px;
    }
  }
  .component-message-wrap {
    z-index: 111;
  }
}
.custom-switch {
  @media (max-width: 767px) {
    padding-left: 0;
  }
}
.custom-switch {
  padding-left: 0;
}
.main-heading {
  h2,
  h4 {
    display: inline-block;
    padding: 5px 10px;
    color: $whiteColor;
    font-weight: 400;
  }
}
.form-control {
  &:focus {
    box-shadow: none;
    border-color: $themeColor;
  }
}
.navbar-collapse {
  flex-grow: 0;
}
.form-group {
  position: relative;
  margin-bottom: 1.5rem;
  input {
    height: 38px;
  }
}

