.ribbon-heading-wrap {
  position: relative;
  height: 30px;
  margin-bottom: 10px;

  .ribbon-heading {
    width: auto;
    height: 40px;
    line-height: 50px;
    padding-left: 15px;
    padding-right: 20px;
    position: absolute;
    left: 0px;
    top: 0px;
    z-index: 1;
    display: flex;
    align-items: center;
    background: $themeColor;
    &.ligh-blue-ribbon {
      background: $lightblueColor;
      height: 30px;

      &:after {
        border-left: 14px solid $lightblueColor;
        border-top: 14px solid transparent;
        border-bottom: 16px solid transparent;
      }
      h4 {
        font-size: 14px;
        color: $whiteColor;
        a {
          color: $whiteColor;
          text-decoration: none;
        }
      }
    }
    &:after,
    &:before {
      position: absolute;
      content: "";
    }
    &:after {
      height: 0;
      width: 0;
      right: -13px;
      border-top: 19px solid transparent;
      border-bottom: 21px solid transparent;
      border-left: 14px solid $themeColor;
      top: 0;
    }
    h2 {
      color: $whiteColor;
      font-size: 18px;
      @media(max-width:767px){
        font-size:14px;
      }
    }
  }
}

.top-pick-section {
  background: $whiteColor;
  padding: 40px;
  margin-bottom: 30px;
  height: 100%;
  @media(max-width:991px){
   padding:40px 15px;
  }
  .carousel {
    width: 60%;
    @media(max-width:991px){
      width:50%;
    }
    @media(max-width:767px){
      width:100%;
      margin-bottom:20px;
    }
  }
  .top-pick-video-wrap {
    height: 300px;
    width: 100%;
    background: $grayColor;
    img {
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
  }
  .top-pick-text {
    width: 40%;
    @media(max-width:991px){
      width:50%;
    }
    @media(max-width:767px){
      width:100%;
    }
    p {
      font-size: 14px;
    }
    a {
      h4 {
        color: $headingColor;
        font-size: 16px;
      }
      &:hover {
        text-decoration: none;
      }
    }
  }
}
.blog-list-wrap {
  padding: 10px 20px;
  background: $whiteColor;
  border-radius: 6px;
  @media(max-width:991px){
    padding:20px 15px;
  }
  .post-thumbnail-wrap {
    width: 40%;
    @media(max-width:767px){
      width:100%;
    }
    .post-thumbnail {
      height: 240px;
      width: 100%;
      @media(max-width:767px){
        height: 170px;
        margin-bottom: 20px;
      }
      img {
        height: 100%;
        width: 100%;
        object-fit: cover;
      }
    }
  }

  .blog-list-content {
    width: 60%;
    @media(max-width:767px){
      width:100%;
    }
    p {
      color: $grayColor;
      font-size: 13px;
    }
    a {
      h5 {
        color: $headingColor;
        font-size: 16px;
      }
      &:hover {
        text-decoration: none;
      }
    }
  }
}
.rection-wrap {
  display: flex;
  align-items: center;

  a {
    font-size: 12px;
    color: $headingColor;
    position: relative;
    align-items: center;
    color: $grayColor;
    text-decoration: none;
    &:after {
      position: absolute;
      top: 0;
      right: -9px;
      content: "|";
      color: $grayColor;
    }
    span {
      margin-right: 4px;
    }
    &:last-child::after {
      display: none;
    }
  }
}
.tag-wrap {
  flex-wrap: wrap;
  a {
    background: $headingColor;
    color: $whiteColor;
    font-size: 12px;
    padding: 3px 10px;
    text-decoration: none;
    margin-bottom: 10px;
  }
}
.social-media-listing {
  .social-icon {
    background: $headingColor;
    color: $whiteColor;
    font-size: 12px;
    padding: 3px 10px;
    text-decoration: none;
    border-radius: 3px;
    cursor: pointer;
  }
}
.main-heading-ribbon {
  margin-bottom: 20px;
  margin-top: 30px;
  .ribbon-heading-wrap {
    width: 80%;
  }
  .view-btn {
    color: $lightblueColor;
    text-decoration: underline;
    &:hover {
      color: $themeColor;
    }
  }
}
.form-group {
  position: relative;
  margin-bottom: 1.5rem;
  .form-control-placeholder {
    position: absolute;
    top: 0;
    padding: 7px 0 0 13px;
    transition: all 200ms;
    opacity: 0.5;
    &.float-label {
      font-size: 75%;
      transform: translate3d(0, -100%, 0);
      opacity: 1;
      padding-left: 0px;
    }
  }
  .form-control {
    &:focus {
      & + .form-control-placeholder {
        font-size: 75%;
        transform: translate3d(0, -100%, 0);
        opacity: 1;
        padding-left: 0px;
      }
    }
  }

  input,
  textarea {
    position: relative;
    z-index: 2;
    background: transparent;
  }
}

.map-wrap {
  width: 100%;
  background: $whiteColor;
  border-radius: 6px;
  margin-top: 40px;
  box-shadow: 1px 1px 12px 5px rgba(204, 204, 204, 0.17);
  height: 100%;
  @media(max-width:991px){
    height: 200px;
  }
  
}
.footer {
  padding: 40px 15px 0;
  margin: 100px 0 0;
  background: $footercolor;
  @media(max-width:991px){
    text-align: center;
    margin:20px 0 0;
  }
  h5 {
    font-size: 18px;
    font-weight: 600;
    color: $headingColor;
  }
  .footer-listing {
    list-style: none;
    padding: 0;
    margin: 0;
    li {
      a {
        display: block;
        color: $headingColor;
        text-decoration: none;
        padding: 3px 0;
        opacity: 0.5;
        transition: all 400ms ease-in-out;
        font-size: 14px;
        font-weight: 500;
        &:hover {
          color: $themeColor;
          opacity: 1;
        }
      }
    }
  }
  p {
    font-size: 14px;
    font-weight: 500;
    color: $headingColor;
    opacity: 0.5;
    max-width: 200px;
    @media(max-width:767px){
      max-width:100%;
    }
  }
  .copyright-section {
    margin: 10px 0;
    p {
      font-size: 12px;
      font-weight: 500;
      color: $headingColor;
      opacity: 0.5;
      max-width: 100%;
    }
  }
  .newslatter-wrap {
    button {
      background: $themeColor;
      color: $whiteColor;
      padding: 0 20px;
      font-size: 12px;
      &:hover,
      &:not(:disabled):not(.disabled):active {
        border-color: $themeColor;
        background: $themeColor;
      }
    }
    input {
      font-size: 12px;
    }
  }
  .btn-outline-secondary:not(:disabled):not(.disabled):active:focus {
    box-shadow: none;
  }
}
.post-details-container {
  .post-details-header {
    //background: white;
  }
  .post-detail-hr {
    border: none;
    height: 1px;
    /* Set the hr color */
    color: grey; /* old IE */
    background-color: grey; /* Modern Browsers */
  }

  .post-comment-item {
    border-radius: 12px;
    box-shadow: 0px 1px 5px 0px $boxShadow;
    display: flex;
    width: 100%;
    .author-pic {
      width: 7%;
    }
    .contents {
      flex-direction: column;
      display: flex;
      width: 93%;
      .more-less {
        color: blue;
        cursor: pointer;
      }
    }
  }

  .blog-category {
    .category-tag {
      background: $headingColor;
      color: $whiteColor;
      font-size: 12px;
      padding: 3px 10px;
      text-decoration: none;
      border-radius: 3px;
    }
  }

  .post-description-container {
    code {
      background-color: $color-code-background;
      color: $color-code-color;
      border-radius: 0.3rem;
      padding: 4px 5px 5px;
      white-space: nowrap;
    }

    pre code {
      white-space: inherit;
    }

    pre {
      background-color: $color-code-background;
      padding: 5px;
      border-radius: 0.3em;
    }
  }

  .featured-image-container {
    .img-container {
      position: relative;
      .feature-image-background {
        filter: blur(8px);
        -webkit-filter: blur(8px);
        height: 100%;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        width: 100%;
        position: absolute;
        z-index: -1;
      }
    }
    .caption {
      width: 100%;
      font-size: 80%;
      font-weight: 400;
      margin-top: 20px;
    }
  }

  .blog-tags {
    .tag {
      background: $headingColor;
      color: #fff;
      font-size: 12px;
      padding: 3px 10px;
      text-decoration: none;
      border-radius: 3px;
    }
  }

  .post-comment-form {
    .form-group {
      margin-bottom: 18px;
    }
  }

  .add-comment {
    position: relative;
    .comments-count {
      position: absolute;
    }
    .form-wrap {
      height: auto;
    }
  }
  .post-title{
    font-size: 24px;
    line-height: 32px;
  }
}
